<template>
    <div ref="map" class="map"></div>
</template>

<script>
    import echarts from 'echarts/lib/echarts';
    import 'echarts/extension/bmap/bmap'
    import style from '@/data/mapStyle2'
    import ComJSON from '@/data/community';
    import OutPJSON from '@/data/outpatient'
    import { yiyuanStr, matouStr, addLocation, huocheStr, feijiStr} from './../data'

    export default {
            name: 'SzMap',
            data () {
                return {
                    chart: echarts.ECharts,
                    bmap: {
                        enableMapClick: false,
                    },
                    linesSeries: [],
                    scatterSeries: [],
                    effectScatterSeries: [],
                    mapZoom: 6,
                }
            },
            mounted () {
                this.initMap()
            },
            methods: {
                initCommunity(){
                    return ComJSON
                        .map(d => ({
                            name: d.name,
                            value: d.coord.concat(5),
                        }))
                },
                initOutpatient() {
                    return OutPJSON.map(d => ({
                        name: d.ORGAN_NAME,
                        value: [d.latitude1, d.longitude1, 100]
                    }))
                },
                initAddLocation(data) {
                    return data.map(d => ({
                        name: d.name,
                        value: d.coord.concat(8),
                    }))
                },
                initMap () { 
                    const communityData = this.initCommunity();
                    const outpatientData = this.initOutpatient();
                    this.chart = echarts.init(this.$refs.map)
                    this.chart.setOption({
                        animation: false,
                        bmap: { 
                            center: [114.16455183658751, 22.628456637984177],
                            zoom: 11,
                            roam: true,
                            mapStyle: {
                                styleJson:  style
                            }
                        },
                        tooltip : {
                            trigger: 'item',
                            formatter: d => {
                                let final = '';
                                switch (d.seriesName) {
                                    case '':
                                    case '':
                                    case '':
                                        final = `
                                                ${d.marker}${d.name}<br />
                                                : &nbsp;&nbsp;&nbsp;<br />
                                                : &nbsp;&nbsp;&nbsp;<br />
                                                : &nbsp;&nbsp;&nbsp;
                                            `
                                        break;
                                    case '': 
                                        final = `
                                            ${d.seriesName}<br />
                                            ${d.marker}${d.name}
                                        `
                                        break;
                                    case '': 
                                        final = `
                                                ${d.marker}${d.name}<br />
                                                : &nbsp;&nbsp;&nbsp;<br />
                                                : &nbsp;&nbsp;&nbsp;<br />
                                                : &nbsp;&nbsp;&nbsp;
                                            `
                                        break;
                                    default:
                                        break;
                                }
                                return final;
                            }
                        },
                        legend: {
                            orient: 'vertical',
                            y: 'bottom',
                            x: 'right',
                            data: ['', '', '', '', ''],
                            textStyle: {
                                color: '#fff'
                            },
                            itemHeight: 14,
                            itemWidth : 14,
                        },
                        series: [{
                            name: '',
                            type: 'effectScatter',
                            coordinateSystem: 'bmap',
                            symbolSize: d => d[2],
                            data: communityData,
                            showEffectOn: 'render',
                            rippleEffect: {
                                brushType: 'stroke'
                            },
                            hoverAnimation: true,
                            zlevel: 1,
                            itemStyle: {
                                color: 'rgba(239, 83, 80, 0.54)',
                                shadowBlur: 0,
                            },
                        }, {
                            name: '',
                            type: 'scatter',
                            coordinateSystem: 'bmap',
                            data: outpatientData,
                            symbolSize: 12,
                            symbol: yiyuanStr,
                            zlevel: 1,
                        }, {
                            name: '',
                            type: 'scatter',
                            coordinateSystem: 'bmap',
                            data: this.initAddLocation(addLocation.filter(d => d.type==="")),
                            symbolSize: 16,
                            symbol: matouStr,
                            zlevel: 2,
                            label: {
                                normal: {
                                    formatter: '{b}',
                                    position: 'right',
                                    show: true,
                                    color: '#fff',
                                },
                                emphasis: {
                                    show: true
                                }
                            },
                        }, {
                            name: '',
                            type: 'scatter',
                            coordinateSystem: 'bmap',
                            data: this.initAddLocation(addLocation.filter(d => d.type==="")),
                            symbolSize: 16,
                            symbol: huocheStr,
                            zlevel: 2,
                            label: {
                                normal: {
                                    formatter: '{b}',
                                    position: 'right',
                                    show: true,
                                    color: '#fff',
                                },
                                emphasis: {
                                    show: true
                                }
                            },
                        }, {
                            name: '',
                            type: 'scatter',
                            coordinateSystem: 'bmap',
                            data: this.initAddLocation(addLocation.filter(d => d.type==="")),
                            symbolSize: 16,
                            symbol: feijiStr,
                            zlevel: 2,
                            label: {
                                normal: {
                                    formatter: '{b}',
                                    position: 'right',
                                    show: true,
                                    color: '#fff',
                                },
                                emphasis: {
                                    show: true
                                }
                            },
                        }
                        ]
                    })
                    // this.bmap = new window.BMap.Map("allmap")
                    this.bmap = this.chart.getModel().getComponent('bmap').getBMap()
                    this.bmap.setMinZoom(10) // 
                    this.bmap.setMaxZoom(15) // 
                    const name = ['', '', '', '', '', '', '', '', '', '']
                    name.map(d => {
                        this.drawLine(`${d}`,  {
                                strokeWeight: 2,
                                strokeColor: 'gray',
                                fillColor: 'rgba(0,0,0,0)',
                            });
                    })
                    this.drawLine(``,  {
                        strokeWeight: 2,
                        strokeStyle: 'dashed',
                        strokeColor: '#23caed',
                        fillColor: 'rgba(0,0,0,0)',
                        strokeOpacity: 1,
                    });
                },
                drawLine (name, style) {
                    var bdary = new window.BMap.Boundary();
                    bdary.get(name, rs => {//
                        // this.bmap.clearOverlays();//
                        var count = rs.boundaries.length; //
                        var pointArray = [];
                        for (var i = 0; i < count; i++) {
                            var ply = new window.BMap.Polygon(rs.boundaries[i], style); //
                            this.bmap.addOverlay(ply);  //
                            pointArray = pointArray.concat(ply.getPath());
                        }    
                        // this.bmap.setViewport(pointArray);    //  
                    });   
                }
            }
        }
</script>

<style lang="less" scoped>
    .map{
        width: 80%;
    }
</style>